#mask {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #ebecef;
    z-index: 999;
}


#progress {
    min-width: 300px;
    width: 40%;
    height: 30px;
    margin: auto;
    z-index: 1000;
    border: 2px solid white;

    .progress-bar {
        background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
    }

}
